"use client";

import { useEffect } from "react";
import { useRouter } from "next/navigation";
import { useSession } from "next-auth/react";
import {
  Box,
  Heading,
  Text,
  Button,
  Flex,
  HStack,
  Badge,
  Spinner,
  Center,
  Table,
} from "@chakra-ui/react";
import DashboardLayout from "../components/layout/DashboardLayout";
import { useQuery } from "@tanstack/react-query";
import { fetchReports } from "../lib/api/reports"; // Backend API client

export default function ReportsPage() {
  const { data: session, status } = useSession();
  const router = useRouter();

  const { data, isLoading, error } = useQuery({
    queryKey: ["reports"],
    queryFn: fetchReports,
  });

  useEffect(() => {
    if (status === "unauthenticated") router.push("/login");
  }, [status, router]);

  if (status === "loading" || isLoading)
    return (
      <Center h="100vh">
        <Spinner size="xl" color="teal.500" />
        <Text ml={4}>Loading reports...</Text>
      </Center>
    );

  if (error)
    return (
      <DashboardLayout>
        <Center h="60vh">
          <Text color="red.500">Failed to load reports.</Text>
        </Center>
      </DashboardLayout>
    );

  return (
    <DashboardLayout>
      <Flex
        justify="space-between"
        align="center"
        mb={8}
        direction={{ base: "column", sm: "row" }}
        gap={4}
      >
        <Box>
          <Heading size="lg" mb={2}>
            Reports
          </Heading>
          <Text color="gray.500">
            Manage and download your generated data exports.
          </Text>
        </Box>
        <HStack>
          <Button variant="outline" size="sm" gap={2}>
            Filter
          </Button>
          <Button colorScheme="teal" size="sm" gap={2}>
            Export All
          </Button>
        </HStack>
      </Flex>

      <Table.Root variant="simple">
        <Table.Header bg="gray.50" _dark={{ bg: "gray.800" }}>
          <Table.Row>
            <Table.ColumnHeader>ID</Table.ColumnHeader>
            <Table.ColumnHeader>Name</Table.ColumnHeader>
            <Table.ColumnHeader>Date</Table.ColumnHeader>
            <Table.ColumnHeader>Status</Table.ColumnHeader>
            <Table.ColumnHeader textAlign="right">Action</Table.ColumnHeader>
          </Table.Row>
        </Table.Header>
        <Table.Body>
          {data?.map((report: any) => (
            <Table.Row
              key={report._id}
              _hover={{ bg: "gray.50/50", _dark: { bg: "gray.800/50" } }}
            >
              <Table.Cell fontWeight="bold">{report._id}</Table.Cell>
              <Table.Cell>{report.name}</Table.Cell>
              <Table.Cell>{new Date(report.date).toLocaleDateString()}</Table.Cell>
              <Table.Cell>
                <Badge
                  colorScheme={report.status === "Generated" ? "green" : "orange"}
                >
                  {report.status}
                </Badge>
              </Table.Cell>
              <Table.Cell textAlign="right">
                <Button variant="ghost" size="xs" colorScheme="teal">
                  View
                </Button>
              </Table.Cell>
            </Table.Row>
          ))}
        </Table.Body>
      </Table.Root>
    </DashboardLayout>
  );
}
